<template lang="pug">
  el-dialog.investors-dialog(:title="investorsDialog.title", v-model="investorsDialog.show", size="small")
    .table
      table
        colgroup
          col(width='20%')
          col(width='40%')
          col(width='20%')
          col(width='20%')
        thead
          tr
            th.text-center 客户名称
            th.text-center 身份证号
            th.text-right 持有金额 (元)
            th.text-right 占比
      div.tbody-table
        table
          colgroup
            col(width='20%')
            col(width='40%')
            col(width='20%')
            col(width='20%')
          //- thead
            tr
              th 客户名称
              th 持有金额（元）
              th 占比
          tbody
            tr(v-if="investorsDialog.investors.length", v-for="investor in investorsDialog.investors")
              td.text-center {{investor.name}}
              td.text-center {{investor.identity_id}}
              td.text-right {{investor.price | ktRound(2)}}
              td.text-right {{investor.percentage | ktPercent}}
            tr(v-if="!investorsDialog.investors.length")
              td.text-center(colspan="3") 无客户信息
</template>

<script>
import {
  Dialog,
  Loading
} from 'element-ui'
import {
  investors
} from '../../../common/resources.js'

export default {
  components: {
    ElDialog: Dialog
  },

  methods: {
    async show(product) {
      let loadingInstance
      this.investorsDialog.show = true
      setTimeout(() => {
        loadingInstance = Loading.service({
          target: '.investors-dialog .el-dialog__body'
        })
      }, 10)

      let data = await investors.get({
        registered_product_id: product.id
      }).then(res => res.json()).catch(() => {
        loadingInstance.close()
      })

      loadingInstance.close()
      this.investorsDialog.title = product.product_code
      this.investorsDialog.investors = data.investors
    }
  },

  data() {
    return {
      investorsDialog: {
        show: false,
        investors: [],
        title: ''
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.tbody-table {
  max-height: 300px;
  overflow-y: auto;
  border-radius: 4px;
}
</style>



